.nav{
  position:fixed;
  top:0;
  padding:20px;
  width:100%;
  height:30px;
  z-index:1;
  transition-timing-function:ease-in;
  transition:all 0.5s;
 
}
.nav_contents{
   display:flex;
   justify-content:space-between;
   
}
.nav_black{
    background-color:#111;
}
.nav_logo{
  position:fixed;
  left:0;
  top:10px;
  width:80px;
  padding-left:20px;
  cursor: pointer;
  object-fit:contain;
  
}
.nav_avatar{
  position:fixed;
  right:20px;
  width:30px;
  cursor: pointer;
}